import React from "react";
export default function NavBar() {
  return (
    <div className="navbar">
      <div className="logo-container">
        <span>logo Picture</span>
      </div>
      <div className="top-menu-right">
        <span className="top-btn" className="top-btn">
          Reservation
        </span>
        <span className="top-btn">Orders</span>
        <span className="top-btn">Location</span>
        <small
          style={{
            color: "snow",
            paddingLeft: "10px",
            paddingRight: "10px",
            paddingTop: "5px",
            paddingBottom: "5px",
            height: "20px",
            width: "30px",
            backgroundColor: "black",
            borderRadius: "20px"
          }}
          className="top-btn"
        >
          Log In
        </small>
        <small
          style={{
            paddingLeft: "10px",
            paddingRight: "10px",
            paddingTop: "5px",
            paddingBottom: "5px",
            height: "20px",
            width: "30px",
            border: "solid black",
            borderRadius: "20px"
          }}
          className="top-btn"
        >
          Sign Up
        </small>
        <span className="top-btn">basket</span>
      </div>
    </div>
  );
}
